import React, { forwardRef } from 'react';
import { bool, node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-wizard-step';
const CSS_STATE = 'vds-state';

const WizardStep = forwardRef(({
  active,
  children,
  className,
  completed,
  error,
  ...remainingProps
}, ref) => (
  <li
    className={classnames(CSS_PREFIX, {
      [`${CSS_STATE}--active`]: Boolean(active),
      [`${CSS_STATE}--completed`]: Boolean(completed),
      [`${CSS_STATE}--error`]: Boolean(error)
    }, className)}
    ref={ref}
    style={active ? { boxShadow: '0 0 0 1px #003ea9' } : undefined}
    {...remainingProps}
  >
    {children}
  </li>
));

WizardStep.propTypes = {
  /**
  * Active
  */
  active: bool,

  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
  * Completed
  */
  completed: bool,

  /**
  * Error
  */
  error: bool
};

WizardStep.displayName = 'WizardStep';

export default WizardStep;
